/**
 * 读取文件
 *
 * @memberof PIXI.utils
 * @function download
 * @param {string} name - 文件名
 * @param {object} data - 文件数据，字符串
 */
export function download(name: string, data: any): void {
    const urlObject: any = window.URL || window.webkitURL || window;

    const downloadData = new Blob([data]);

    const saveLink = document.createElementNS('http://www.w3.org/1999/xhtml', 'a') as any;

    saveLink.href = urlObject.createObjectURL(downloadData);
    saveLink.download = name;
    window.open(saveLink);
}
